export default "import React, { useState, useCallback } from 'react';\nimport { Slider, Space } from 'antd';\nimport { ZoomInOutlined, ZoomOutOutlined } from '@ant-design/icons';\n\nexport default function ZoomInOutSlider({ onScaleChange }) {\n  const [scale, setScale] = useState(100);\n  const onSlideValueChange = useCallback(\n    value => {\n      setScale(value);\n      onScaleChange(value);\n    },\n    [onScaleChange],\n  );\n  return (\n    <Space>\n      <ZoomOutOutlined />\n      <Slider defaultValue={scale} onChange={onSlideValueChange} />\n      <ZoomInOutlined />\n    </Space>\n  );\n}\n";