export default "@import '../../styles/mixins';\n@conector-size: 16px;\n\n.complex-sample {\n  .ant-slider {\n    min-width: 180px;\n  }\n  &-edge-label {\n    text-align: center;\n    margin-top: -20px;\n  }\n  &-start-node,\n  &-end-node,\n  &-joint-node {\n    border: 1px solid black;\n    border-radius: 50%;\n    width: 30px;\n    height: 30px;\n    background: white;\n  }\n\n  &-joint-node {\n    font-size: 18px;\n    display: flex;\n    align-content: center;\n    justify-content: center;\n    align-items: center;\n  }\n\n  &-end-node::before {\n    content: '';\n    position: absolute;\n    border: 1px solid black;\n    border-radius: 50%;\n    background: black;\n    top: 5px;\n    left: 5px;\n    width: 20px;\n    height: 20px;\n  }\n\n  &-connector {\n    &-in,\n    &-out {\n      width: @conector-size;\n      height: @conector-size;\n      border: 1px solid black;\n      border-radius: 50%;\n      position: absolute;\n      margin: auto;\n      top: 0;\n      bottom: 0;\n      background: white;\n    }\n\n    &-in {\n      left: @conector-size / -2;\n    }\n    &-out {\n      right: @conector-size / -2;\n    }\n  }\n\n  &-node {\n    width: 100%;\n    height: 100%;\n    &-group-content,\n    &-content {\n      position: relative;\n      width: 100%;\n      height: 100%;\n      border: 1px solid #8799c1;\n      border-radius: 10px;\n      display: flex;\n      flex-direction: column;\n    }\n    &-group-content {\n      padding: 4px 20px;\n      > div {\n        height: 30px;\n\n        a {\n          margin-left: 6px;\n          font-size: 16px;\n        }\n      }\n    }\n    &-content {\n      align-items: center;\n      justify-content: center;\n    }\n  }\n\n  &-nice-dag-wrapper {\n    height: 400px;\n  }\n}";